$output-bourbon-deprecation-warnings: false;
@import "bourbon";
@import "vars";

@media screen and (max-width: 1600px) {
	.main.new-year {

		header {
			&:before {
				@include size(148px 271px); }

			&:after {
				@include size(153px 422px); } }

		.m_bl_bottom_bls {

			&:before {
				top: -105px;
				@include size(142px 366px); }

			&:after {
				right: 85px;
				bottom: -120px;
				@include size(100px 174px); } }

		.m_bl_articles {
			position: relative;

			&:before {
				left: 138px;
				top: -95px;
				@include size(100px 137px); }

			&:after {
				right: 0;
				top: 100%;
				@include size(111px 317px); } }

		.banner-top__cont {

			&:before {
				left: 0;
				top: 150px;
				@include size(100px 190px); }


			&:after {
				top: calc(100% - 30px);
				@include size(130px 298px);
				display: none; } }

		.m_bl_banners +.m_popular_products {

			&:before {
				@include size(110px 317px);
				display: none; }


			&:after {
				right: 50px;
				top: calc(100% - 277px);
				@include size(100px 180px); } } } }

@media screen and (max-width: 1500px) {

	.products_btn_more {
		padding: 45px 0; }

	.main.new-year {

		header {
			&:before {
				@include size(100px 271px); }

			&:after {
				@include size(100px 422px); } }

		.m_bl_bottom_bls {

			&:before {
				top: -105px;
				@include size(142px 366px); }

			&:after {
				right: 0;
				bottom: -120px;
				@include size(100px 174px); } }

		.m_bl_articles {
			position: relative;

			&:before {
				left: 0;
				top: -95px;
				@include size(100px 137px); }

			&:after {
				right: 0;
				top: 100%;
				@include size(100px 317px); } }

		.banner-top__cont {

			&:before {
				left: 0;
				top: 150px;
				@include size(100px 190px); }


			&:after {
				top: calc(100% - 30px);
				@include size(100px 298px);
				display: none; } }

		.m_bl_banners +.m_popular_products {

			&:before {
				@include size(100px 317px);
				display: none; }


			&:after {
				right: 0;
				top: calc(100% - 277px);
				@include size(90px 180px); } } } }

@media screen and (max-width: 1300px) {

	.search-top {
		margin-bottom: 45px; } }

@media screen and (max-width: 1200px) {
	body {
		background: #f5f6f7; }

	.bl_one_product__slider {
		background: #fff;
		padding: 20px; }

	.menu_forms-list {
		padding: 10px; }

	.bl_basket_s_top_tx {

		+ .clearfix {
			display: flex;
			flex-direction: row-reverse;

			.bl_basket_s_zakaz {
				float: none;
				margin-left: 2em; }

			.bl_basket_s_zakaz_form {
				margin-right: auto;
				width: 100%;

				.form_row {

					p {
						position: static;
						display: block;
						width: 100%;
						margin-bottom: 7px; }

					input {
						margin-left: 0; }

					+ .form_row {
						margin-top: 15px; }

					textarea {
						margin-left: 0; } } } } }




	.bl_apt_row > div {
		background: #f5f6f7; }

	.bl_apt_row .bl_apt_item_addr span, .bl_apt_item_status.status_yellow:before {
		background-color: #f5f6f7; }
	.new-order-make .order_page .bl_one_product_avail_cont .btn {
		width: auto;
		white-space: nowrap; }

	.banner {
		padding: 90px 50px; }

	.products_btn_more {
		padding: 35px 0; }

	#content {
		padding-top: 60px; }

	.m_popular_products_cont_item {
		width: 950px; }

	.new-order-make .order_page .radio__label span,
	.new-order-make .order_page .bl_apt_item_tel span,
	.new-order-make .order_page .bl_apt_item_status span,
	.bl_apt_item_status.status_green:before,
	.bl_apt_item_status.status_red:before {

		background-color: #f5f6f7 !important; } }


@media screen and (min-width: 1100px) {
	.page_usercart {
		.product_row_left {
			max-width: 100%;
			width: 55%; } }



	.bl_basket_s_zakaz_form {

		.form_row {
			display: flex;
			flex-direction: column;

			p {
				position: static;
				width: 100%; }

			input, textarea {
				margin-left: 0; }



			+ .form_row {
				margin-top: 15px; } } }

	.radio {
		max-width: 310px;

		&__label {
			padding-left: 25px; } }

	.bl_apt_item_tel {
		max-width: 170px; }

	.bl_basket_s_zakaz_select_shop {
		.bl_apt_row .bl_apt_item_status {
			width: 200px; }

		+ .bonus-my {
			margin-top: 40px; }

		+ .bonus-my .bonus-my__text {
			padding: 30px; } }


	.bonus-card {
		margin-right: 50px; }

	.bonus-my {
		&__point, &__sale {
			font-size: 20px;

			span {
				font-size: 25px; } }

		&__text {
			> *:not(:first-child) {
				margin-top: 15px; } } }


	.bonus {
		&-title {
			font-size: 22px;
			margin-bottom: 16px;
			margin-top: 35px; }

		&-target__item {
			padding: 25px; }

		&__item {
			&-title {
				font-size: 22px;
				margin-bottom: 35px;
				line-height: 1.2; }

			&-point {
				font-size: 25px;
				margin-bottom: 15px; } } }

	.faq {
		margin-top: 46px;

		&__item {
			&-title {
				padding: 21px;

				&:after {
					right: 20px; } } } }

	.sale__item {
		padding: 25px 70px 25px 25px;
		background-position: right 25px center; } }


@media screen and (max-width: 820px) {

	.new-order-make .order_page .bl_apt_row > div {
		width: 100% !important; }

	.new-order-make .order_page .bl_one_product_avail_cont .btn {
		margin-left: 20px; }

	.sale {
		margin: 35px 0;

		&__list {
			margin: 0 0 -20px 0; }

		&__item {
			width: 100%;
			margin: 0 0 20px 0; } }

	.bonus-target__item {
		width: calc((100% - 20px * 2) / 2);
		margin: 0 0 20px 20px; }

	.bl_basket_s_zakaz_form {
		max-width: 100%; }

	.bl_apt_row {
		width: 100%; }

	.cart-table {
		.left {
			width: 65px;

			img {
				width: 65px; } } }

	.cart-table {
		td {
			padding: 10px; } }

	.radio {
		max-width: 215px; }

	.bl_apt_item_tel {
		max-width: 160px; }

	.bl_one_product_avail_cont {
		width: 100%; }

	.bl_apt_row {
		padding: 0 45px 0 0;

		&__top:before {
			display: none; } }

	.bl_apt_item_tel {
		padding-left: 0 !important;
		margin-right: 10px;

		&:before {
			display: none; } }

	.btn, .btn_back {
		white-space: nowrap;
		height: 34px;
		line-height: 34px;
		padding: 0 15px;
		font-size: 14px; }




	.bl_basket_s_zakaz_bottom_checkboxs {
		padding-top: 20px; }

	.bl_apt_row {
		box-sizing: border-box;
		padding: 0;

		&.info-open {
			border: 2px solid #f5f6f7;
			padding: 21px; } }

	.cart {
		&-table {
			display: block;

			thead, tr, td, th {
				display: flex; }

			tr {
				width: 100%;
				flex-wrap: wrap;
				justify-content: space-between;
				align-items: center; }

			th {
				&:not(:first-child) {
					display: none; }

				&:nth-child(1) {
					width: 100%; } }


			td {
				&:nth-child(1) {
					width: 100%;
					padding-right: 10px;
					padding-bottom: 0; } }

			.right {
				margin-bottom: auto; } }

		&-title {
			margin-bottom: 4px !important; }

		&-price {
			position: relative;
			display: flex;
			flex-wrap: wrap;
			align-items: flex-start;

			&:before {
				content: 'Цена';
				display: block;
				color: #000;
				font-size: 16px;
				width: 100%;
				margin-bottom: 3px;
				text-align: left; } }

		&-quant {
			position: relative;
			display: flex;
			flex-wrap: wrap;
			align-items: flex-start;

			&:before {
				content: 'Количество';
				display: block;
				color: #000;
				font-size: 16px;
				width: 100%;
				margin-bottom: 3px;
				text-align: left; } }

		&-total {
			position: relative;
			display: flex;
			flex-wrap: wrap;
			align-items: flex-start;

			&:before {
				content: 'Сумма';
				display: block;
				color: #000;
				font-size: 16px;
				width: 100%;
				margin-bottom: 3px;
				text-align: left; } } }

	.bl_basket_s_zakaz_bottom_col {
		margin-top: 25px;
		width: 100%; }

	.bl_basket_s_zakaz_select_shop + .bonus-my {
		margin-top: 25px; }

	.bl_basket_s_zakaz_bottom, .bl_basket_s_zakaz_select_shop {
		padding-top: 0; }

	.bl_one_product_avail_cont .btn {
		width: auto; } }


@media screen and (max-width: 660px) {

	.fancybox-inner, .fancybox-skin, .fancybox-wrap {
		width: 100% !important;
		left: 0 !important;
		padding: 0 !important; }
	.popup {
