			padding: 21px;
			margin-bottom: 20px;

			&__num {
				margin-top: 15px; } } }

	.bl_basket_s_zakaz_bottom_btns {
		> * {
			&:not(:first-child) {
				margin-top: 15px; } } }

	.bl_basket_s_zakaz_bottom_col p {
		margin-bottom: 0; }

	.bonus {
		&-title {
			font-size: 19px;
			margin-bottom: 12px;
			margin-top: 30px; }

		&-target {
			&__list {
				margin: 0 0 -30px 0; }

			&__item {
				width: 100%;
				margin: 0 0 30px 0;
				padding: 21px; } }

		&__item {
			&-title {
				font-size: 18px;
				margin-bottom: 20px; }

			&-point {
				font-size: 20px;
				margin-bottom: 5px;

				span {
					font-size: 25px; } } } }

	.form-col .form_row {
		max-width: 190px; } }


@media screen and (max-width: 1000px) {

	.banner {
		width: 100%;
		margin: 0 0 20px 0;
		&-top {
			flex-direction: column;

			.m_popular_products {
				max-width: 100%;

				&_cont_item {
					margin: 0; } } } }

	#content {
		padding-top: 0; }

	.m_popular_products_cont_item {
		margin: 0 25px !important;
		width: auto !important; } }




@media screen and (max-width: 820px) {
	.p_search_name {
		width: 83% !important; }

	.basket-header {
		padding: 15px 0;
		padding: 5px 0 5px 0;

		&__email, &__pdf, &__print {
			margin-left: 1.4rem; } }

	.product_count input {
		background: transparent; } }




@media screen and (max-width: 660px) {

